.colorpicker {
  width: 356px;
  height: 176px;
  overflow: hidden;
  position: absolute;
  font-family: Arial, Helvetica, sans-serif;
  display: none;
}

.colorpicker_color {
  width: 150px;
  height: 150px;
  left: 14px;
  top: 13px;
  position: absolute;
  background: #f00;
  overflow: hidden;
  cursor: crosshair;
}

.colorpicker_color div {
  position: absolute;
  top: 0;
  left: 0;
  width: 150px;
  height: 150px;
}

.colorpicker_color div div {
  position: absolute;
  top: 0;
  left: 0;
  width: 11px;
  height: 11px;
  overflow: hidden;
  margin: -5px 0 0 -5px;
}

.colorpicker_hue {
  position: absolute;
  top: 13px;
  left: 171px;
  width: 35px;
  height: 150px;
  cursor: n-resize;
}

.colorpicker_hue div {
  position: absolute;
  width: 35px;
  height: 9px;
  overflow: hidden;
  margin: -4px 0 0 0;
  left: 0px;
}

.colorpicker_new_color {
  position: absolute;
  width: 60px;
  height: 30px;
  left: 213px;
  top: 13px;
  background: #f00;
}

.colorpicker_current_color {
  position: absolute;
  width: 60px;
  height: 30px;
  left: 283px;
  top: 13px;
  background: #f00;
}

.colorpicker input {
  background-color: transparent;
  border: 1px solid transparent;
  position: absolute;
  font-size: 10px;
  font-family: Arial, Helvetica, sans-serif;
  color: #898989;
  top: 4px;
  right: 11px;
  text-align: right;
  margin: 0;
  padding: 0;
  height: 11px;
}

.colorpicker_hex {
  position: absolute;
  width: 72px;
  height: 22px;
  left: 212px;
  top: 142px;
}

.colorpicker_hex input {
  right: 6px;
}

.colorpicker_field {
  height: 22px;
  width: 62px;
  background-position: top;
  position: absolute;
}

.colorpicker_field span {
  position: absolute;
  width: 12px;
  height: 22px;
  overflow: hidden;
  top: 0;
  right: 0;
  cursor: n-resize;
}

.colorpicker_rgb_r {
  top: 52px;
  left: 212px;
}

.colorpicker_rgb_g {
  top: 82px;
  left: 212px;
}

.colorpicker_rgb_b {
  top: 112px;
  left: 212px;
}

.colorpicker_hsb_h {
  top: 52px;
  left: 282px;
}

.colorpicker_hsb_s {
  top: 82px;
  left: 282px;
}

.colorpicker_hsb_b {
  top: 112px;
  left: 282px;
}

.colorpicker_submit {
  position: absolute;
  width: 22px;
  height: 22px;
  left: 322px;
  top: 142px;
  overflow: hidden;
}

.colorpicker_focus {
  background-position: center;
}

.colorpicker_hex.colorpicker_focus {
  background-position: bottom;
}

.colorpicker_submit.colorpicker_focus {
  background-position: bottom;
}

.colorpicker_slider {
  background-position: bottom;
}

.converter .colorpicker {
  width: unset;
  height: unset;
  background: unset;
  display: flex !important;
  flex-direction: row-reverse;
}

.converter .colorpicker_color, .converter .colorpicker_hue, .converter .colorpicker_new_color {
  position: relative;
  left: unset;
  top: unset;
}

.converter .colorpicker_current_color, .converter .colorpicker_hex, .converter .colorpicker_field, .converter .colorpicker_submit {
  display: none;
}

.converter .colorpicker_new_color {
  height: unset;
  flex-grow: 1;
  width: unset;
  z-index: 1;
  border-radius: 10px 0 0 10px;
}

.converter .colorpicker_hue {
  background: linear-gradient(to top, #ff0000 0%, #ffff00 17%, #00ff00 33%, #00ffff 50%, #0000ff 66%, #ff00ff 83%, #ff0000 100%);
  width: 6px;
  z-index: 2;
}

.converter .colorpicker_hue div {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 1px solid #dcdcdc;
  background: unset;
  left: 50%;
  transform: translateX(-50%);
}

.converter .colorpicker_color {
  border-radius: 0 10px 10px 0;
}

.converter .colorpicker_color:before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: '';
  background: linear-gradient(to right, white 0%, rgba(255, 255, 255, 0) 100%);
}

.converter .colorpicker_color div {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, black 100%);
}

.converter .colorpicker_color div div {
  width: 10px;
  height: 10px;
  border: 1px solid #dcdcdc;
  border-radius: 50%;
  background: unset;
  background-color: rgba(196, 196, 196, 0.2);
}

html * {
  box-sizing: border-box;
}

#wrapper {
  position: relative;
  overflow: hidden;
  background-color: #ffffff;
}

body {
  min-width: 320px;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  color: #000000;
  font-size: 14px;
}

a {
  text-decoration: none;
  transition: all 0.3s ease;
  color: #000000;
}

a:hover {
  text-decoration: none;
  transition: all 0.3s ease;
  color: #000000;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0 0 10px;
}

img {
  max-width: 100%;
}

p {
  margin: 0 0 10px;
}

ul, li, ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

address {
  font-style: normal;
}

input {
  border: 0;
  outline: 0;
}

input[type="text"], input[type="password"], input[type="email"], input[type="search"] {
  -webkit-appearance: none;
}

button {
  border: 0;
  outline: 0;
  padding: 0;
  background-color: unset;
  cursor: pointer;
}

textarea {
  resize: vertical;
  -webkit-appearance: none;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.img-bg {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.present-section .container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.converter {
  padding: 10px;
  width: 290px;
}

.converter-col {
  margin-bottom: 10px;
}

.converter-col:last-child {
  margin-bottom: 0;
}

.colorpick {
  border-radius: 10px;
  border: 1px solid #dcdcdc;
}

.form-control {
  width: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  color: #777777;
  padding: 2px 12px;
  line-height: 34px;
  border: 1px solid #dcdcdc;
  outline: none;
}

.form-control:focus {
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.15);
}
